body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 14px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

::-webkit-scrollbar {
    display: none;
}

button {
    border: none;
    background: none;
}

::-moz-selection {
    background: #a8d1ff;
}

::selection {
    background: #a8d1ff;
}

::-moz-selection {
    background: #a8d1ff;
}

::-webkit-selection {
    background: #a8d1ff;
}


