@-webkit-keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.Cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1000;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-animation: appear 500ms;
            animation: appear 500ms;
}

.Cover .Cover-Title {
    margin-top: -10vh;
    font-size: 22px;
    line-height: 36px;
    margin-bottom: 12px;
    color: #444;
}

.Cover .Cover-Description {
    font-size: 13px;
    line-height: 22px;
    color: #666;
}



